import React, { useEffect, useState } from 'react';
import { List, Card } from 'antd';
import request from '../../fetdata';

const pageSize = 10;

const NewsList = ({ title, url, dataPath, svg, backgroundColor }) => {
  const [page, setPage] = useState(1);
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);

  const loadMoreData = () => {
    setLoading(true);
    request({
      url,
    }).then((response) => {
      setData(response.slice((page - 1) * pageSize, page * pageSize));
      setPage(page + 1);
    }).catch((error) => {
      console.log(error.message);
    }).finally(() => {
      setLoading(false);
    });
  };

  useEffect(() => {
    loadMoreData();
  });

  return (
    <Card
      headStyle={{ background: backgroundColor }}
      bodyStyle={{ height: '32rem' }}
      title={
        <div style={{ display: 'flex' }}>
          <img src={svg} style={{ width: '3rem', height: '3rem' }} alt="" />
          <h2 style={{ marginLeft: '5px', marginTop: '5px' }}>{title}</h2>
        </div>
      }
    >
      <List
        loading={loading}
        dataSource={data}
        dataLength={data.length}
        next={loadMoreData}
        hasMore={data.length < pageSize}
        renderItem={(item, index) => (
          <List.Item>
            {
              index < 3
                ? <a href={item.url}><span style={{ color: '#f1403c' }} target="_blank" >[{index + 1}]</span> {item[dataPath]}</a>
                : <a href={item.url}><span style={{ color: '#c2a469' }} target="_blank" >[{index + 1}]</span> {item[dataPath]}</a>
            }
          </List.Item>
        )}
      />
    </Card>
  );
}

export default NewsList;
